.brand-list-container {
    width: 100%;
    margin: 0 auto;
   
    overflow-y: auto;
    text-align: center;
    background-color: #e8e8e8;
}
 
.brand-list-title {
    font-size:20px;
    margin-bottom: 05px;
}
 
.search-bar {
    padding: 10px;
    width: 50%;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin: auto;
}
 
.card-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
   
}
 
.brand-card {
    width: 150px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: center;
    transition: transform 0.3s;
    
}
 
.brand-card:hover {
    transform: translateY(-5px);
}
 
.logo-container {
    width: 100px;
    height: 100px;
    margin: 0 auto 15px;
}
 
.brand-logo {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}
 
.brand-name {
    font-size: 1.2em;
    margin-bottom: 10px;
    color: #333;
}
 
.update-button {
    background-color: #28a745;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 0.9em;
    transition: background-color 0.3s;
}
 
.update-button:hover {
    background-color: #218838;
}
 
.loading-message,
.error-message {
    font-size: 1.5em;
    color: #333;
    display: flex;
    height: 50vh;
    align-items: center;
    justify-content: center;
}
 
 
.product-list-card-container1000 {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); /* Decreased card size */
    gap: 20px;
}
 
.product-list-card1000 {
    background-color: white;
    border-radius: 10px; /* More rounded corners */
    padding: 5px; /* Decreased padding */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    transition: transform 0.3s, box-shadow 0.3s, background-color 0.3s; /* Added background-color transition */
    position: relative; /* For animation effects */
}
 
.product-list-card1000:hover {
    transform: translateY(-5px) scale(1.05); /* Scale effect on hover */
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
    background-color: #f1f1f1; /* Change background color on hover */
}
 
.product-list-image1000 {
    width: 100%;
    height: auto;
    border-radius: 5px;
    transition: transform 0.3s; /* Smooth image transition */
}
 
/* .product-list-card1000:hover .product-list-imageV025 {
    transform: scale(1.1); 
} */
 
.product-list-titleV1000 {
    font-size: 16px; /* Decreased title size */
    font-weight: 600;
    margin: 10px 0;
    color: #333; /* Dark color for the product title */
}
 
.product-list-description1000 {
    font-size: 12px; /* Decreased description size */
    color: #555;
    height: 40px; /* Fixed height to limit text overflow */
    overflow: hidden; /* Hide overflow text */
}
 
.product-list-volume1000,
.product-list-price1000 {
    font-size: 14px; /* Decreased font size */
    font-weight: bold;
    margin: 5px 0;
    color: #333; /* Darker color for contrast */
}
 
.product-list-image1000 {
    width: 100px;
    height: 100px;
    border-radius: 5px;
    margin-top: 10px;
    transition: transform 0.3s; /* Smooth image transition */
    mix-blend-mode: multiply;
}

.back-button-cont1000{
   display: flex;
  margin-bottom: 5px;
   
}

.back-button1000 {
    z-index:1000;
    position: fixed;
  top:1000;
  
  
    color: black;
    border-color: black;
    border-radius:8px;
    
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-left: 5px;
    margin-bottom: 10px;
    
}

.back-button1000:hover {
    background-color: #6EC207;
    color: black;
}
.product-head-1000{
    text-align: center;
    margin: auto;
}